.catalog {
  display: flex;
  flex-wrap: wrap;
  padding: 50px 0;

  max-width: 944px;
  margin: 0 auto;
  perspective: 400px;

}

.card {
  margin-bottom: 48px;
  display: flex;
  flex-direction: column;
  padding: 32px 16px;
  padding-bottom: 16px;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;

  &__img {
    margin-left: 3px;
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__name {
    height: 36px;
    margin-bottom: 4px;
    text-decoration: none;
    color: #060b35;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
  }

  &__code {
    margin-bottom: 16px;
    font-weight: normal;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
  }

  &__response {
    margin-bottom: 24px;
    height: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__stars-4 {
    display: flex;
  }

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    flex-grow: 0;
  }

  &__stars-1 > :nth-child(-n+1),
  &__stars-2 > :nth-child(-n+2),
  &__stars-3 > :nth-child(-n+3),
  &__stars-4 > :nth-child(-n+4),
  &__stars-5 > :nth-child(n) {
    background-image: url(../images/star-active.svg);
  }

  &__star:not(:last-child) {
    margin-right: 4px;
  }

  &__response-count {
    font-size: 10px;
    line-height: 14px;
    flex-grow: 0;
  }

  &__price {
    margin-bottom: 16px;
    height: 18px;
    display: flex;
    justify-content: space-between;
  }

  &__price-title {
    font-size: 12px;
    line-height: 18px;
    color: #616070;
  }

  &__price-value {
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    color: #060b35;
  }

  &__button {
    display: flex;
    justify-content: center;
    text-align: center;
    background: #00acdc;
    border-radius: 5px;
    width: 166px;
    height: 40px;
  }

  &__button:hover {
    background: #fff;
    border: 1px solid #00acdc;
    border-radius: 5px;
    color: #00acdc;
  }

  &__link {
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    line-height: 40px;
    font-size: 14px;
    text-transform: uppercase;
    font-style: normal;
    padding-left: 56px;
    padding-right: 56px;
  }

  &__link:hover {
    color: #00acdc;
  }

}

.card:nth-last-child(-n + 4) {
  margin-bottom: 0;
}

.card:not(:nth-child(4n)) {
  margin-right: 48px;
}
